
.contact {
    padding: 10px;
    list-style: none;
   border-bottom : 4px solid #e3e3e3 ;
    margin: 0;

}
.contact-sub-menu {
    padding: 0;
    margin: 0;
    display: none;
    list-style: none;
}
.contact-sub-item{
    list-style: none;
}
.contact-sub-menu .active{
    margin: 0;
    display: block;
}
.contact-item{
    cursor: pointer;
    display: flex;
    justify-content: space-between;
}
